import CssBaseline from '@mui/material/CssBaseline';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import React from 'react';
import { Box, Container, styled, SxProps, Theme, Toolbar } from '@mui/material';
import Footer from './footer';
import Topbar from './topbar';
import Nav from './nav';
import './styles/mui-icon.css';

export const DRAWER_WIDTH = 260;

const theme = createTheme({
  palette: {
    primary: {
      main: '#31322c',
    },
    background: {
      default: '#fafafa',
    },
  },
});

const App = styled('div')(() => ({
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100vh',
}));

const Document = (props: {
  children: React.ReactNode;
  hiddenNav?: boolean;
  removeContainer?: boolean;
  sx?: SxProps<Theme>;
}) => {
  const { hiddenNav, removeContainer } = props;
  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <App>
          <CssBaseline />
          <Topbar />
          <Toolbar />
          <Box
            sx={[
              { display: 'flex', position: 'relative', flex: 1 },
              ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
            ]}
          >
            {!hiddenNav && <Nav />}
            {removeContainer ? (
              props.children
            ) : (
              <Container>{props.children}</Container>
            )}
          </Box>
          <Box
            sx={{ mt: 'auto', ml: hiddenNav ? undefined : `${DRAWER_WIDTH}px` }}
          >
            <Footer />
          </Box>
        </App>
      </ThemeProvider>
    </React.Fragment>
  );
};

export default Document;
