import {
  createTheme,
  Drawer,
  List,
  ThemeProvider,
  Toolbar,
} from '@mui/material';
import React, { FC, useMemo } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { DRAWER_WIDTH } from './document';
import SubMenu from './components/sub-menu';
import LinkMenu from './components/link-menu';

export type MdxNode = {
  id?: string;
  fields: {
    slug: string;
  };
  frontmatter?: {
    subheader?: string;
    description?: string;
    tags?: string[];
    title?: string;
    subtitle?: string;
    order?: number;
    icon?: string;
  };
};

export type MdxMenus = {
  nodes: MdxNode[];
  subMenus?: {
    subheader: string;
    children: MdxNode[];
  }[];
};

const Nav: FC = () => {
  const {
    allMdx: { nodes },
  } = useStaticQuery(graphql`
    query NavQuery {
      allMdx {
        nodes {
          id
          fields {
            slug
          }
          frontmatter {
            tags
            title
            subtitle
            order
            description
            subheader
            icon
          }
        }
      }
    }
  `);

  const menus: MdxMenus = useMemo(() => {
    const sortMenu = (nodes as MdxNode[]).sort(
      (prev, next) =>
        (prev.frontmatter?.order ?? 1) - (next.frontmatter?.order ?? 1)
    );
    const singleMenus = sortMenu.filter((item) => !item.frontmatter?.subheader);
    const subheaderMenus = sortMenu.filter(
      (item) => !!item.frontmatter?.subheader
    );
    const subheaders: string[] = Array.from(
      new Set(subheaderMenus.map((item) => item.frontmatter?.subheader || ''))
    );
    return {
      nodes: singleMenus,
      subMenus: subheaders.map((name) => {
        return {
          subheader: name,
          children: subheaderMenus.filter(
            (item) => item.frontmatter?.subheader === name
          ),
        };
      }),
    };
  }, [nodes]);

  return (
    <ThemeProvider
      theme={() =>
        createTheme({
          palette: {
            mode: 'dark',
          },
        })
      }
    >
      <Drawer
        variant="persistent"
        anchor="left"
        open
        sx={{
          width: DRAWER_WIDTH,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: DRAWER_WIDTH,
            boxSizing: 'border-box',
          },
        }}
      >
        <Toolbar />
        <List>
          {menus.nodes?.map((node) => (
            <LinkMenu node={node} key={node.id} />
          ))}
          {menus.subMenus?.map((subMenu, index) => (
            <SubMenu
              nodes={subMenu.children}
              subheader={subMenu.subheader}
              key={index}
            />
          ))}
        </List>
      </Drawer>
    </ThemeProvider>
  );
};

export default Nav;
